"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'

const AiSimulation = () => {
  const [siwakScore, setSiwakScore] = useState<number | null>(null)
  const [umkmResult, setUmkmResult] = useState<string | null>(null)
  const [loading, setLoading] = useState(false)

  // Mock data untuk simulasi
  const mockCompanies = [
    { name: "PT Energi Hijau Nusantara", sector: "Energi Terbarukan", risk: 25 },
    { name: "CV Organik Sejahtera", sector: "Pertanian", risk: 45 },
    { name: "PT Greenwash Industries", sector: "Manufaktur", risk: 85 },
    { name: "UD Limbah Bersih", sector: "Pengelolaan Limbah", risk: 15 },
    { name: "PT Eco Fake Solutions", sector: "Teknologi", risk: 92 }
  ]

  const simulateSiWaK = () => {
    setLoading(true)
    setTimeout(() => {
      const randomCompany = mockCompanies[Math.floor(Math.random() * mockCompanies.length)]
      setSiwakScore(randomCompany.risk)
      setLoading(false)
    }, 2000)
  }

  const simulateUMKMScreening = () => {
    setLoading(true)
    setTimeout(() => {
      const outcomes = [
        "✅ LULUS SCREENING - Sertifikat hijau akan diterbitkan dalam 3 hari kerja",
        "⚠️ PERLU VERIFIKASI TAMBAHAN - Dokumen keuangan perlu dilengkapi",
        "❌ TIDAK MEMENUHI SYARAT - Belum memiliki praktik berkelanjutan yang memadai",
        "🔄 DALAM REVIEW - Tim verifikasi akan menghubungi dalam 24 jam"
      ]
      const randomOutcome = outcomes[Math.floor(Math.random() * outcomes.length)]
      setUmkmResult(randomOutcome)
      setLoading(false)
    }, 1500)
  }

  return (
    <div className="grid lg:grid-cols-2 gap-8">
      {/* SiWaK Model Simulation */}
      <Card>
        <CardHeader>
          <CardTitle className="text-[#004890]">SiWaK Model - Deteksi Greenwashing</CardTitle>
          <p className="text-sm text-gray-600">
            Sistem AI dengan Random Forest + NLP untuk mendeteksi risiko greenwashing dengan akurasi {'>'}90%
          </p>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="company-data">Data Perusahaan (Simulasi)</Label>
            <Textarea
              id="company-data"
              placeholder="Masukkan laporan emisi, data IoT, dan laporan keuangan..."
              className="min-h-[100px]"
            />
          </div>
          
          <Button 
            onClick={simulateSiWaK}
            disabled={loading}
            className="w-full bg-[#004890] hover:bg-blue-700"
          >
            {loading ? "Menganalisis..." : "Analisis Risiko Greenwashing"}
          </Button>

          {siwakScore !== null && (
            <div className="mt-4 p-4 border rounded-lg">
              <div className="flex items-center justify-between mb-2">
                <span className="font-semibold">Skor Risiko:</span>
                <span className={`text-2xl font-bold ${
                  siwakScore > 70 ? 'text-red-600' : 
                  siwakScore > 40 ? 'text-yellow-600' : 'text-green-600'
                }`}>
                  {siwakScore}/100
                </span>
              </div>
              
              <div className="w-full bg-gray-200 rounded-full h-2 mb-3">
                <div 
                  className={`h-2 rounded-full ${
                    siwakScore > 70 ? 'bg-red-500' : 
                    siwakScore > 40 ? 'bg-yellow-500' : 'bg-green-500'
                  }`}
                  style={{ width: `${siwakScore}%` }}
                ></div>
              </div>

              {siwakScore > 70 && (
                <div className="bg-red-50 border border-red-200 rounded p-3">
                  <p className="text-red-800 font-semibold">⚠️ RISIKO TINGGI</p>
                  <p className="text-red-700 text-sm">
                    Proyek akan diselidiki oleh OJK. Insentif KLM dibekukan sementara.
                  </p>
                </div>
              )}

              {siwakScore <= 70 && siwakScore > 40 && (
                <div className="bg-yellow-50 border border-yellow-200 rounded p-3">
                  <p className="text-yellow-800 font-semibold">⚠️ RISIKO SEDANG</p>
                  <p className="text-yellow-700 text-sm">
                    Diperlukan audit tambahan dan monitoring ketat.
                  </p>
                </div>
              )}

              {siwakScore <= 40 && (
                <div className="bg-green-50 border border-green-200 rounded p-3">
                  <p className="text-green-800 font-semibold">✅ RISIKO RENDAH</p>
                  <p className="text-green-700 text-sm">
                    Proyek memenuhi standar transparansi dan keberlanjutan.
                  </p>
                </div>
              )}
            </div>
          )}

          <div className="text-xs text-gray-500 mt-4">
            <p><strong>Contoh Deteksi:</strong></p>
            <p>Proyek X melaporkan reduksi emisi 10.000 ton, tetapi data IoT menunjukkan peningkatan 5%. 
            AI mendeteksi anomali → proyek masuk daftar pantau.</p>
          </div>
        </CardContent>
      </Card>

      {/* UMKM Green FastTrack */}
      <Card>
        <CardHeader>
          <CardTitle className="text-[#004890]">UMKM Green FastTrack</CardTitle>
          <p className="text-sm text-gray-600">
            AI Screening untuk sertifikasi hijau ekspres UMKM dalam {'<'}24 jam
          </p>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="umkm-name">Nama UMKM</Label>
              <Input id="umkm-name" placeholder="PT/CV/UD..." />
            </div>
            <div className="space-y-2">
              <Label htmlFor="umkm-sector">Sektor</Label>
              <Input id="umkm-sector" placeholder="Manufaktur, Pertanian, dll" />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="npwp">NPWP (Data Ditjen Pajak)</Label>
            <Input id="npwp" placeholder="XX.XXX.XXX.X-XXX.XXX" />
          </div>

          <div className="space-y-2">
            <Label htmlFor="slik">Nomor SLIK OJK</Label>
            <Input id="slik" placeholder="Historis kredit dari SLIK OJK" />
          </div>

          <Button 
            onClick={simulateUMKMScreening}
            disabled={loading}
            className="w-full bg-[#004890] hover:bg-blue-700"
          >
            {loading ? "Screening AI..." : "Mulai AI Screening"}
          </Button>

          {umkmResult && (
            <div className="mt-4 p-4 border rounded-lg">
              <h4 className="font-semibold mb-2">Hasil Screening:</h4>
              <p className="text-sm">{umkmResult}</p>
              
              <div className="mt-3 text-xs text-gray-600">
                <p><strong>Proses Selanjutnya:</strong></p>
                <ul className="list-disc list-inside space-y-1 mt-1">
                  <li>Verifikasi lapangan oleh mobile unit</li>
                  <li>Pendampingan teknis oleh fintech partner</li>
                  <li>Integrasi dengan BI GreenHub</li>
                </ul>
              </div>
            </div>
          )}

          <div className="bg-blue-50 border border-blue-200 rounded p-3 text-sm">
            <p className="font-semibold text-blue-800">💡 Info Program:</p>
            <p className="text-blue-700">
              UMKM menyumbang 60% emisi sektor industri. Program ini mengakselerasi 
              partisipasi mereka dengan penyederhanaan sistem dan biaya sertifikasi.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}

export default AiSimulation
