"use client"

import { useState } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

const CarbonClaim = () => {
  const [claimResult, setClaimResult] = useState<any>(null)
  const [loading, setLoading] = useState(false)

  const mockProjects = [
    {
      id: "PRJ001",
      name: "Solar Panel Installation PT ABC",
      sector: "Energi Terbarukan",
      reduction: 1250,
      status: "Verified",
      bonus: 250000
    },
    {
      id: "PRJ002", 
      name: "Waste Management System CV XYZ",
      sector: "Pengelolaan Limbah",
      reduction: 890,
      status: "Pending",
      bonus: 178000
    },
    {
      id: "PRJ003",
      name: "Organic Farming UD Hijau",
      sector: "Pertanian",
      reduction: 2100,
      status: "Verified",
      bonus: 420000
    }
  ]

  const recentClaims = [
    {
      company: "PT Energi Bersih Nusantara",
      amount: "Rp 1.250.000",
      co2: "6.25 ton",
      date: "15 Jan 2025",
      status: "Dibayar"
    },
    {
      company: "CV Organik Sejahtera",
      amount: "Rp 890.000", 
      co2: "4.45 ton",
      date: "12 Jan 2025",
      status: "Diproses"
    },
    {
      company: "UD Limbah Bersih",
      amount: "Rp 2.100.000",
      co2: "10.5 ton",
      date: "10 Jan 2025", 
      status: "Dibayar"
    }
  ]

  const simulateClaim = () => {
    setLoading(true)
    setTimeout(() => {
      const randomProject = mockProjects[Math.floor(Math.random() * mockProjects.length)]
      setClaimResult(randomProject)
      setLoading(false)
    }, 2000)
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Dibayar': return 'text-green-600 bg-green-100'
      case 'Diproses': return 'text-yellow-600 bg-yellow-100'
      case 'Verified': return 'text-green-600 bg-green-100'
      case 'Pending': return 'text-yellow-600 bg-yellow-100'
      default: return 'text-gray-600 bg-gray-100'
    }
  }

  return (
    <div className="space-y-8">
      {/* Statistics Cards */}
      <div className="grid md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-[#004890]">2.8M</div>
            <p className="text-sm text-gray-600">Ton CO₂ Tereduksi</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-[#004890]">Rp 560M</div>
            <p className="text-sm text-gray-600">Total Bonus Dibayar</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-[#004890]">1,247</div>
            <p className="text-sm text-gray-600">Proyek Terverifikasi</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-[#004890]">Rp 200</div>
            <p className="text-sm text-gray-600">Bonus per Ton CO₂</p>
          </CardContent>
        </Card>
      </div>

      <div className="grid lg:grid-cols-2 gap-8">
        {/* Carbon Claim Simulator */}
        <Card>
          <CardHeader>
            <CardTitle className="text-[#004890]">Simulasi Carbon Claim</CardTitle>
            <p className="text-sm text-gray-600">
              Hitung estimasi bonus untuk reduksi CO₂ proyek Anda
            </p>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="project-name">Nama Proyek</Label>
              <Input id="project-name" placeholder="Masukkan nama proyek..." />
            </div>

            <div className="space-y-2">
              <Label htmlFor="sector">Sektor</Label>
              <Select>
                <SelectTrigger>
                  <SelectValue placeholder="Pilih sektor" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="energi">Energi Terbarukan</SelectItem>
                  <SelectItem value="limbah">Pengelolaan Limbah</SelectItem>
                  <SelectItem value="pertanian">Pertanian Organik</SelectItem>
                  <SelectItem value="manufaktur">Manufaktur Hijau</SelectItem>
                  <SelectItem value="transportasi">Transportasi Berkelanjutan</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="co2-reduction">Estimasi Reduksi CO₂ (ton/tahun)</Label>
              <Input id="co2-reduction" type="number" placeholder="0" />
            </div>

            <Button 
              onClick={simulateClaim}
              disabled={loading}
              className="w-full bg-[#004890] hover:bg-blue-700"
            >
              {loading ? "Menghitung..." : "Hitung Bonus Carbon Claim"}
            </Button>

            {claimResult && (
              <div className="mt-4 p-4 border rounded-lg bg-green-50">
                <h4 className="font-semibold text-green-800 mb-2">Hasil Simulasi:</h4>
                <div className="space-y-2 text-sm">
                  <p><strong>Proyek:</strong> {claimResult.name}</p>
                  <p><strong>Reduksi CO₂:</strong> {claimResult.reduction} ton/tahun</p>
                  <p><strong>Bonus per 6 bulan:</strong> Rp {claimResult.bonus.toLocaleString()}</p>
                  <p><strong>Bonus per tahun:</strong> Rp {(claimResult.bonus * 2).toLocaleString()}</p>
                  <p><strong>Status:</strong> 
                    <span className={`ml-1 px-2 py-1 rounded text-xs ${getStatusColor(claimResult.status)}`}>
                      {claimResult.status}
                    </span>
                  </p>
                </div>
              </div>
            )}

            <div className="bg-blue-50 border border-blue-200 rounded p-3 text-sm">
              <p className="font-semibold text-blue-800">💡 Cara Kerja:</p>
              <ol className="list-decimal list-inside space-y-1 text-blue-700 mt-2">
                <li>Upload laporan emisi ke BI GreenHub</li>
                <li>AI verifikasi otomatis reduksi CO₂</li>
                <li>Bonus Rp200/ton ditransfer setiap 6 bulan</li>
                <li>Monitoring berkelanjutan untuk konsistensi</li>
              </ol>
            </div>
          </CardContent>
        </Card>

        {/* Recent Claims */}
        <Card>
          <CardHeader>
            <CardTitle className="text-[#004890]">Klaim Terbaru</CardTitle>
            <p className="text-sm text-gray-600">
              Daftar pembayaran bonus carbon claim terbaru
            </p>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {recentClaims.map((claim, index) => (
                <div key={index} className="border-b pb-3 last:border-b-0">
                  <div className="flex justify-between items-start mb-1">
                    <h4 className="font-semibold text-sm">{claim.company}</h4>
                    <span className={`px-2 py-1 rounded text-xs ${getStatusColor(claim.status)}`}>
                      {claim.status}
                    </span>
                  </div>
                  <div className="text-sm text-gray-600">
                    <p><strong>Bonus:</strong> {claim.amount}</p>
                    <p><strong>CO₂ Tereduksi:</strong> {claim.co2}</p>
                    <p><strong>Tanggal:</strong> {claim.date}</p>
                  </div>
                </div>
              ))}
            </div>

            <div className="mt-6 p-4 bg-gray-50 rounded-lg">
              <h4 className="font-semibold text-[#004890] mb-2">Statistik Bulanan</h4>
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <p className="text-gray-600">Total Klaim</p>
                  <p className="font-bold">47 proyek</p>
                </div>
                <div>
                  <p className="text-gray-600">Total Bonus</p>
                  <p className="font-bold">Rp 9.4M</p>
                </div>
                <div>
                  <p className="text-gray-600">CO₂ Tereduksi</p>
                  <p className="font-bold">47 ton</p>
                </div>
                <div>
                  <p className="text-gray-600">Rata-rata/Proyek</p>
                  <p className="font-bold">Rp 200K</p>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Process Flow */}
      <Card>
        <CardHeader>
          <CardTitle className="text-[#004890]">Alur Proses Carbon Claim</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid md:grid-cols-4 gap-4">
            <div className="text-center">
              <div className="w-12 h-12 bg-[#004890] text-white rounded-full flex items-center justify-center mx-auto mb-3 font-bold">
                1
              </div>
              <h4 className="font-semibold mb-2">Input Data</h4>
              <p className="text-sm text-gray-600">
                Upload laporan emisi dan data proyek ke platform BI GreenHub
              </p>
            </div>
            <div className="text-center">
              <div className="w-12 h-12 bg-[#004890] text-white rounded-full flex items-center justify-center mx-auto mb-3 font-bold">
                2
              </div>
              <h4 className="font-semibold mb-2">Verifikasi AI</h4>
              <p className="text-sm text-gray-600">
                Sistem otomatis hitung reduksi CO₂ dan deteksi anomali
              </p>
            </div>
            <div className="text-center">
              <div className="w-12 h-12 bg-[#004890] text-white rounded-full flex items-center justify-center mx-auto mb-3 font-bold">
                3
              </div>
              <h4 className="font-semibold mb-2">Approval</h4>
              <p className="text-sm text-gray-600">
                Review dan persetujuan oleh tim verifikasi Bank Indonesia
              </p>
            </div>
            <div className="text-center">
              <div className="w-12 h-12 bg-[#004890] text-white rounded-full flex items-center justify-center mx-auto mb-3 font-bold">
                4
              </div>
              <h4 className="font-semibold mb-2">Pembayaran</h4>
              <p className="text-sm text-gray-600">
                Bonus Rp200/ton CO₂ ditransfer ke rekening setiap 6 bulan
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}

export default CarbonClaim
